// installhttps://uviewui.com/components/vueUse.html

const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: "https://www.hzsysf.com/mall",
		// baseUrl: "http://47.103.104.99/mall",
		// baseUrl: "http://192.168.10.25:8084/mall/",
		// baseUrl: "http://feiyang.nat300.top/mall",
		loadingText: '~',
		loadingTime: 800,
		showLoading: true,
		originalData: true,
		loadingMask: true, // loading
		// content-type
		header: {
			"Content-Type": "application/x-www-form-urlencoded"
		}
		// ......
	});

	// 
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// token
		// tokenStorage
		// Storage
		const token = uni.getStorageSync('token');
		config.header.token = token;
		// console.log(token);
		// config.header.Content-Type ="multipart/form-data";


		// urlurlthis.$u.get(url)url
		// if(config.url == '/user/login') config.header.noToken = true;
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	}


	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		console.log("")
		console.log(res)

		if (res.data.status == 0) {
			// rescoderesult
			// res.resultthis.$u.post(url).then(res => {})thenres
			// originalDatatrue
			return res.data;
		} else if (res.data.status == 10) {
			console.log("");
			vm.$u.toast('');
			//  token








			setTimeout(() => {
				uni.reLaunch({
					url: "/pages/welcome/wx-authorization/wx-authorization"
				})
			}, 1500)


			return false;
		} else {
			// vm.$u.toast(res.data.msg); //
			// falsePromisereject
			// this.$u.post(url).then().catch(res=>{})catchres
			return false;
		}
	}
}

export default {
	install
}
